import React, {useState, useEffect} from "react";

const Header = ()=>{

    const [hambureger, sethamburger] = useState("")
    const [navMenu, setNavMenu] = useState("hidden")

    const toggleHamburger = ()=>{
        
        sethamburger(hambureger === "" ? "active" : "")
        setNavMenu(navMenu === "" ? "hidden" : "")

    }
    const [navFixed, setnavFixed] = useState("")

    useEffect(_ =>{
        
        const scrollHandler = ()=>{

            if(window.pageYOffset > 1){
                setnavFixed("navbar-fixed")
            }else{
                setnavFixed("")
            }
        }      
        
        window.addEventListener('scroll', scrollHandler)
        return _ => {
          window.removeEventListener('scroll', scrollHandler)
        }

    }, [])

    const DataHeader = [

        {
            id:1,
            name: 'Home',
            tag: 'home',
        },
        {
            id:2,
            name: 'Skills',
            tag: 'skills',
        },
        {
            id:3,
            name: 'About',
            tag: 'about',
        },
        {
            id:4,
            name: 'Portfolio',
            tag: 'portfolio',
        },
        {
            id:5,
            name:'Experience',
            tag: 'experience',
        },
        {
            id:6,
            name:'Contact',
            tag: 'contact',
        }
        
    ]

    return(
        <header className={`absolute top-0 left-0 z-10 flex items-center w-full bg-transparent ${navFixed}`}>
             <div className="container">
                <div className="relative flex items-center justify-between">
                    <div>
                        <img src="/logo.png" width="70" className="block py-6 text-4xl font-bold text-utama"></img>
                    </div>
                    <div className="flex items-center px-4">
                        <button onClick={toggleHamburger}  id="hamburger" name="hamburger" className={`fixed block right-7 ${hambureger} lg:hidden`}>
                            <span className="transition duration-500 ease-in-out origin-top-left hamburger-garis"></span>
                            <span className="transition duration-500 ease-in-out hamburger-garis"></span>
                            <span className="transition duration-500 ease-in-out origin-bottom-left hamburger-garis"></span>
                        </button>
                        <nav id="nav-menu" className={`${navMenu} absolute py-5 bg-white font-bold shadow-lg rounded-lg max-w-[250px] w-full right-4 top-full lg:block lg:static lg:max-w-full lg:bg-transparent lg:shadow-none lg:rounded-none`}>
                            <ul className="block lg:flex">
                                {
                                    DataHeader.map((data, key) => (
                                    <li className="group">
                                        <a href={`#${data.tag}`} className="flex py-2 mx-8 text-base text-hitam group-hover:text-utama">{data.name}</a>
                                    </li>
                                    ))
                                }
                            </ul>
                        </nav>
                    </div>
                </div>
             </div>   
        </header>
    )
    
};

export default Header;