import React from 'react';
 
// Importing toastify module
import {toast} from 'react-toastify';
 
// Import toastify css file
import 'react-toastify/dist/ReactToastify.css';
 
 // toast-configuration method,
 // it is compulsory method.
toast.configure()

const Contact = () =>{
    return(
        <section id="contact" className="pt-32 pb-32">
            <div data-aos="zoom-in" className="container">
                <div className="w-full px-4">
                    <div className="max-w-xl mx-auto mb-16 text-center">
                        <h2 className="text-3xl font-bold text-hitam">Contact</h2>
                        <p className="text-sm font-bold text-gray-500">Send Message</p>
                    </div>
                </div>
                <form onSubmit={handleSubmit} name="mboiscodex-contact">
                    <div class="w-full lg:w-1/2 lg:mx-auto">
                        <div class="">
                        <div className="w-full px-4 mb-8">
                        <label for="name" className="text-base font-bold text-utama">Name</label>
                        <input required name="name" type="text" id="name" className="w-full p-3 border border-gray-100 rounded-md focus:outline-none focus:bg-white focus:border-black bg-slate-100" placeholder="Full Name" />
                    </div>
                    <div className="w-full px-4 mb-8">
                        <label for="email" className="text-base font-bold text-utama">Email</label>
                        <input required name="email" type="email" id="email" className="w-full p-3 border border-gray-100 rounded-md focus:outline-none focus:bg-white focus:border-black bg-slate-100" placeholder="Email Adress" />
                    </div>
                    <div className="w-full px-4 mb-8">
                        <label for="pesan" className="text-base font-bold text-utama">Message</label>
                        <textarea required name="message" type="text" id="pesan" className="w-full h-32 p-3 border border-gray-100 rounded-md focus:outline-none focus:bg-white focus:border-black bg-slate-100" placeholder="Bio"></textarea>
                    </div>
                    <div class="w-full md:w-1/2 md:mx-auto px-12 ">
                        <button type="submit" class="px-16 w-full rounded-full outline-none hover:opacity-70 text-md font-semibold text-base active:outline-utama py-2 bg-utama text-white hover:shadow-lg transition duration-500 ease-in-out">Submit</button>
                    </div>                            
                        </div>
                    </div>
                </form>
            </div>
        </section>
        
    )
  
  function handleSubmit(e) {
    e.preventDefault();
    const scriptURL = 'https://script.google.com/macros/s/AKfycbyk6ydADElopgtPCh_Pl_mVLjC2CDzVL7zSqCZydcyftmIGC3RD_9iqE1rX4DPtkwGA/exec'
    const form = document.forms['mboiscodex-contact']
  
    fetch(scriptURL, { method: 'POST', body: new FormData(form)})
        .then(
            toast.success('pesan berhasil dikirim !', {autoClose:3000})
            )
  }

  
}

export default Contact;